@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600;700&display=swap');


/* Reset CSS global */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  position: relative;
  background-color: #171717;
  background-size: contain;
  background-position: top;
  z-index: 1;
}

#root::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.048); /* Couleur blanche semi-transparente */
  backdrop-filter: brightness(1.2); /* Éclaircir légèrement */
  z-index: -1; /* Garder le pseudo-élément derrière */
  pointer-events: none; /* Ignorer les clics */
}
/* Styles du body */
body {
  margin: 0;
  font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  color: #333;
}

/* Style du code (non modifié) */
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* Forcer l'affichage des flèches dans react-slick */
.slick-prev,
.slick-next {
  display: block !important;
  z-index: 2;
  width: 40px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
}

.slick-prev {
  left: 10px;
}

.slick-next {
  right: 10px;
}

.slick-prev::before,
.slick-next::before {
  color: white;
}

.slick-prev:hover,
.slick-next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

/* Responsive styles */
@media (max-width: 768px) {
  body {
    font-size: 14px;
  }

  .slick-prev,
  .slick-next {
    width: 30px;
    height: 30px;
  }
}

/* Utility classes */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}
